<template>
    <div class="container p-0 text-center">
        <div class="btn-group" role="group" aria-label="Basic outlined example">
            <!-- 上一頁 -->
            <button type="button" class="btn btn-outline-primary px-2" v-if="pagination.has_pre" @click="$emit('changePage', pagination.current_page-1 )"><i class="bi bi-chevron-left"></i></button>
            <!-- 分頁按鈕 active -->
            <!-- <button type="button" class="btn btn-outline-primary active" v-for="(item) in pagesNumber" :key="item" @click="$emit('changePage',item)">{{item}}</button> -->
            <!-- 分頁按鈕 not active -->
            <button type="button" class="btn btn-outline-primary" v-for="(item) in pagesNumber" :key="item" @click="$emit('changePage',item)">{{item}}</button>
            <!-- 下一頁 -->
            <button type="button" class="btn btn-outline-primary px-2" v-if="pagination.has_next" @click="$emit('changePage', pagination.current_page+1 )"><i class="bi bi-chevron-right"></i></button>
        </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      pagination: {},
      pagesNumber: []
    }
  },
  props: ['paginationReceived'],
  watch: {
    paginationReceived () {
      this.pagination = this.paginationReceived
      this.pagesNumber = []
      console.log(this.pagination)
      for (let i = 1; i <= this.pagination.total_pages; i++) {
        this.pagesNumber.push(`${i}`)
      }
      console.log('this.pagesNumber', this.pagesNumber)
    }
  }
}
</script>
