<!-- eslint-disable vue/multi-word-component-names -->
<template>
    <div class="toast mt-3 me-3" role="alert" aria-live="assertive" aria-atomic="true">
        <div class="toast-header">
            <strong class="me-auto">Bootstrap</strong>
            <small>11 mins ago</small>
            <button type="button" class="btn-close" data-bs-dismiss="toast" aria-label="Close"></button>
        </div>
        <div class="toast-body">
            <strong v-if="success==false">
                產品新增/編輯 失敗
                <small class="d-block" v-for="(item) in message" :key="item">{{item}}</small>
            </strong>
            <strong v-if="success==true">產品新增/編輯 成功</strong>
        </div>
    </div>
</template>

<script>
import Toast from 'bootstrap/js/dist/toast'
export default {
  data () {
    return {
      success: '',
      message: '',
      toastList: {}
    }
  },
  methods: {
    toastShow (success, message) {
      console.log(this.toastList)
      this.toastList.forEach((item) => {
        this.success = success
        this.message = message
        console.log(this.success, this.message)
        item.show()
      })
    }
  },
  mounted () {
    const toastElList = document.querySelectorAll('.toast')
    const toastList = [...toastElList].map(toastEl => new Toast(toastEl, { delay: 6000 }))
    this.toastList = toastList
  }
}
</script>
